/**
 * @format
 */
import 'react-native-gesture-handler';
import React from 'react';
import {AppRegistry, Platform, Text, TextInput} from 'react-native';
import {PaperProvider} from 'react-native-paper';
import {GestureHandlerRootView} from 'react-native-gesture-handler';
import theme from 'theme';
import App from './App';
import {name as appName} from './app.json';
import {styles} from './style';

//redux setup imports
import {Provider} from 'react-redux';
import {PersistGate} from 'redux-persist/integration/react';
import {persistor, store} from './src/reduxSlice/store';
// repack
import {Federated, Script, ScriptManager} from '@callstack/repack/client';
import {version as appVersion} from './package.json';
import getContainersURL from './getContainersURL';

export const MainApp = () => {
  Text.defaultProps = Text.defaultProps || {};
  Text.defaultProps.maxFontSizeMultiplier = 1.2; // the maximum amount the font size will scale.
  TextInput.defaultProps = Text.defaultProps || {};
  TextInput.defaultProps.maxFontSizeMultiplier = 1.2; // the maximum amount the font size will scale.
  TextInput.defaultProps.placeholderTextColor = theme.color.text.tertiary; // Setting the default fallback color for TextInput placeholder
  TextInput.defaultProps.color = theme.color.text.tertiary; // Setting the default fallback color for TextInput placeholder
  Text.defaultProps.color = theme.color.text.tertiary; // Setting the default fallback color for TextInput placeholder
  return (
    <React.Suspense fallback={<Text>Loading...</Text>}>
      <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
          <PaperProvider>
            <GestureHandlerRootView style={styles.gestureRootStyle}>
              <App />
            </GestureHandlerRootView>
          </PaperProvider>
        </PersistGate>
      </Provider>
    </React.Suspense>
  );
};

ScriptManager.shared.addResolver(async (scriptId, caller) => {
  const containersURL = getContainersURL({
    version: appVersion,
    platform: Platform.OS,
    appName,
  });
  const response = await fetch(containersURL);

  const containers = await response.json();

  const resolveURL = Federated.createURLResolver({
    containers,
  });

  let url;
  if (caller === 'main') {
    url = Script.getDevServerURL(scriptId);
  } else {
    url = resolveURL(scriptId, caller);
  }

  if (!url) {
    return undefined;
  }

  return {
    url,
    cache: false, // For development
    query: {
      platform: Platform.OS,
    },
  };
});

AppRegistry.registerComponent(appName, () => MainApp);
